import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import RecommendedBlogs from '~/components/RecommendedBlogs.vue'
export default {
  head() {
    return {
      title: 'Blog:  ' + this.title,
      meta: [
        {
          hid: 'Blog',
          name: 'Blog',
          content: 'Blog' + this.title
        },
        {
          hid: 'twitter:title',
          name: 'twitter:title',
          content: this.title
        },
        {
          hid: 'twitter:description',
          name: 'twitter:description',
          content: this.headline
        },
        {
          hid: 'twitter:image',
          name: 'twitter:image',
          content: this.image
        },
        {
          hid: 'twitter:image:alt',
          name: 'twitter:image:alt',
          content: this.title
        },
        {
          hid: 'og:title',
          property: 'og:title',
          content: this.title
        },
        {
          hid: 'og:description',
          property: 'og:description',
          content: this.headline
        },
        {
          hid: 'og:image',
          property: 'og:image',
          content: this.image
        },
        {
          hid: 'og:image:secure_url',
          property: 'og:image:secure_url',
          content: this.image
        },
        {
          hid: 'og:image:alt',
          property: 'og:image:alt',
          content: this.title
        },
        {
          hid: 'twitter:site',
          name: 'twitter:site',
          content: 'https://twitter.com/InhinyeruC'
        }
      ]
    }
  },
  data: () => ({
    selection: '',
    posts: {
      content: [],
      headline: '',
      image: '',
      date: '',
      author: ''
    },
    image: '',
    headline: '',
    title: '',
    pageload: true,
    loading: false,
    page: 2,
    selection: 0,
    content: [],
    length: null,
    loading: false,
    transparent: 'rgba(255, 255, 255, 0)',
    blogs_load: false,
    news_load: false,
    posts_load: false,
    selection: 0,
    content: [],
    news: [],
    posts: [],
    nuxt: [],
    selectedItem: 0,
    show: true,
    title: '',
    model: 6,
    rounded: ['0', 'sm', 'md', 'lg', 'xl', 'pill', 'circle'],
    links: [],

    posts_content: 0,
    news_content: 0,

    tags: [],
    load_tags: false,
    total_tags: 0,
    height: '200px',
    blogs_load: false,
    blogs_content: [],
    blogs_total: 0,

    nuxt: [],
    laravel: [],
    vue: [],
    load_nuxt: false,
    load_laravel: false,
    load_vue: false,

    show_nuxt: false,
    show_laravel: false,
    show_vue: false,
    blogs_in_random: [],
    blogs_in_latest: []
  }),
  async asyncData({ $axios, error, params }) {
    const dev_front = process.env.DEV_FRONT
    const prod_front = process.env.PROD_FRONT
    const front =
      process.env.NODE_ENV === 'development' ? dev_front : prod_front
    try {
      let response = await $axios.$get(`api/blog/${params.id}`)
      let image_hid
      if (response.data[0].image == null) {
        image_hid = front + '/images/default.jpg'
      } else {
        image_hid = response.data[0].image
      }
      return {
        posts: response.data[0],
        pageload: false,
        slug: params.slug,
        title: response.data[0].title,
        headline: response.data[0].headline,
        tags: response.data[0].tags,
        image: image_hid
      }
    } catch (err) {
      error({ statusCode: 500, message: 'Page not found' })
    }
  },
  computed: {
    id() {
      return this.$route.params.id
    }
  },
  async fetch() {
    const BlogsRandom = await this.$axios.$get(`api/blog/index/10`)
    this.blogs_in_random = BlogsRandom.data

    const Blogslatest = await this.$axios.$get(`api/blog/latest/10`)
    this.blogs_in_latest = Blogslatest.data

    this.load_tags = true
    const tags = await this.$axios.$get(`api/tags`)
    this.load_tags = false
    this.tags = tags.data
    this.total_tags = tags.total
  },
  mounted() {},
  components: { RecommendedBlogs },
  watch: {},
  methods: {}
}
